<template>
  <!-- 视频主页面  默认视频页 -->
  <div class="videolist">
    <div class="videolist__head">
      <!-- 当前分类 -->
      <div class="videolist__head__current iconfont" @click="showTag = !showTag">{{ currentTag }}&nbsp;
        <div class="videolist__head__current__tags" v-show="showTag">
          <!-- 分类列表 -->
          <div :class="{videolist__head__current__tags__all: true, 'videolist__head__current__tags__all-active': currentTag == '全部视频'}"
          @click="getAll()">全部视频</div>
          <div class="videolist__head__current__tags__line"></div>
          <div class="videolist__head__current__tags__grap">
            <div :class="{videolist__head__current__tags__grap__item: true, 'videolist__head__current__tags__grap__item-active': item.name == currentTag}"
            v-for="item in tagList"
            @click="getVideo(item.name, item.id)"
            :key="item.id">
              {{ item.name }}
            </div>
          </div>
        </div>
      </div>
      <!-- 分类 -->
      <div class="videolist__head__category">
        <div :class="{videolist__head__category__item: true,'videolist__head__category__item-active': item.name == currentTag}"
        v-for="item in cateList"
        @click="getVideo(item.name, item.id)"
        :key="item.id">{{ item.name }}</div>
      </div>
    </div>
    <div class="videolist__list">
      <VideoCoverVue v-for="item in videoList"
      :key="item.data.vid"
      :item="item"></VideoCoverVue>
    </div>
  </div>
</template>

<script>
import { getCateList, getTagList, getTagVideo, getAllVideo } from '../../api/api_video'
import VideoCoverVue from './VideoCover.vue'
export default {
  name: 'VideoList',
  components: { VideoCoverVue },
  data () {
    return {
      currentTag: '全部视频',
      showTag: false,
      videoList: null,
      tagList: null,
      cateList: null
    }
  },
  methods: {
    async getData () {
      let res = await getCateList()
      this.cateList = res.data.data
      res = await getTagList()
      this.tagList = res.data.data
      res = await getAllVideo()
      this.videoList = res.data.datas
      for (let i = 5; i > 0; i--) {
        res = await getAllVideo()
        this.videoList.push(...res.data.datas)
      }
    },
    async getVideo (name, id) {
      let res = await getTagVideo(id)
      this.videoList = res.data.datas
      for (let i = 5; i > 0; i--) {
        res = await getTagVideo(id)
        this.videoList.push(...res.data.datas)
      }
      this.currentTag = name
    },
    async getAll () {
      const res = await getAllVideo()
      this.videoList = res.data.datas
      this.currentTag = '全部视频'
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.videolist{
  padding: 20px;
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__current{
      position: relative;
      width: 100px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;
      border: 1px solid #d9d9d9;
      font-size: 12px;
      color: #111111;
      cursor: pointer;
      &__tags{
        position: absolute;
        top: 40px;
        padding: 10px;
        width: 650px;
        // height: 490px;
        // overflow: auto;
        border: 1px solid #f7f7f7;
        border-radius: 5px;
        background-color: white;
        &__all{
          margin: 15px 0;
          padding: 0 15px;
          border-radius: 15px;
          width: max-content;
          height: 30px;
          font-size: 11px;
          text-align: left;
          color: #111111;
          cursor: pointer;
          &-active{
            background-color: #fdebeb;
            color: #ec4141;
          }
          &:hover{
            color: #ec4141;
          }
        }
        &__line{
          margin-bottom: 10px;
          width: 100%;
          border: 1px solid #f7f7f7;
        }
        &__grap{
          display: grid;
          grid-template-columns: repeat(6, 1fr);
          gap:20px;
          &__item{
            font-size: 11px;
            color: #111111;
            text-align: center;
            cursor: pointer;
            &-active{
              color: #ec4141;
              background-color: #fdebeb;
            }
            &:hover{
              color: #ec4141;
            }
          }
        }
      }
    }
    &__category{
      display: flex;
      align-items: center;
      height: 30px;
      &__item{
        margin-left: 15px;
        padding: 0 10px;
        width: max-content;
        height: 20px;
        border-radius: 10px;
        font-size: 12px;
        color: #111111;
        background-color: white;
        cursor: pointer;
      }
      &__item-active{
        color: #ec4141;
        background-color: #fdebeb;
      }
    }
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid: 20px;
  }
}
</style>
